import connect from '@vkontakte/vkui-connect-promise';
import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import React from 'react';

import { Friends, Account, Users } from './api';

import Home from './panels/Home';
import VueFriends from './panels/VueFriends';

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			fetchedUser: null,
			access_token: '10e034eddee1017397f6fb967350f1daf7621e9f3387c100763dbd90ec76dff932db8d2c5732c3b9f33bc',
			friendsIds: null,
			usersInfo: []
		};
	}

	componentDidMount() {
		connect.send("VKWebAppGetAuthToken", {"app_id": 6940370, "scope": "friends,status,wall,groups,photos,video,pages,notes,docs,stats,market"})
			.then(res => {
				console.log('VKWebAppGetAuthToken', res);
				// this.setState({ access_token: res.data.access_token });
				this.getFriends();
			})
			.catch(e => console.error(e));
		connect.send('VKWebAppGetUsersInfo', {})
			.then(res => this.setState({ fetchedUser: res.data }))
			.catch(e => console.error(e));
			
		this.getFriends();
	}

	getFriends() {
		Friends.getFriends({
			count: 5000,
			access_token: this.state.access_token
		})
		.then(data => {
			this.setState({ friendsIds: data.response.items });
			this.getUsersInfo();
		})
		.catch(e => console.error(e));
	}
	getUsersInfo () {
		const strFriendsIds = this.state.friendsIds.slice(0, 100).join(',');
		Users.getUsers({
			user_ids: strFriendsIds,
			fields: 'photo_200_orig,status',
			access_token: this.state.access_token
		})
		.then(data => {
			this.setState((state) => {
				state.usersInfo.push(...data.response);
			});
		})
		.catch(e => console.error(e));
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to });
	};

	render() {
		return (
			<View activePanel={this.state.activePanel}>
				<Home id="home" fetchedUser={this.state.fetchedUser} go={this.go} access_token={this.state.access_token} />
				<VueFriends
					id="friends"
					go={this.go}
					access_token={this.state.access_token}
					usersInfo={this.state.usersInfo}
				/>
			</View>
		);
	}
}

export default App;
