import { Panel, Footer, Button, Group, Div, Avatar, PanelHeader, HeaderButton, Cell, IOS, Spinner,InfoRow, Progress } from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

import React from 'react';

import PropTypes from 'prop-types';

import { Friends } from '../api';

class VueFriends extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			progress: 0,
			countFriends: 0,
			isDelAllFriends: false
		};
	}
	deleteFriend (user_id) {
		return () => {
			Friends.deleteFriends({
				user_id,
				access_token: this.props.access_token
			})
			.then(data => {
				if(data.error) {
					console.error('Ошибка удаления', data.error);
				} else {
					const indexDelFriend = this.props.usersInfo.findIndex(item => item.id === user_id);
					this.props.usersInfo.splice(indexDelFriend, 1);
					this.forceUpdate();
				}
			})
			.catch(e => console.error(e));
		};
	}
	deleteAllFriends () {
		const isClearInterval = !this.props.usersInfo.length;
		this.setState({isDelAllFriends: true});
		this.interval = setInterval(() => {
			if (isClearInterval) clearInterval(this.interval);

			Friends.deleteFriends({
				user_id: this.props.usersInfo[0].id,
				access_token: this.props.access_token
			})
			.then(data => {
				if(data.error) {
					console.error('Ошибка удаления', data.error);
				} else {
					this.props.usersInfo.splice(0, 1);
					this.setState(state => state.progress++);
					this.forceUpdate();
				}
			})
			.catch(e => console.error(e));
		}, 3000);
	}
	cancelDelFriends () {
		clearInterval(this.interval);
		this.setState({isDelAllFriends: false });
	}
	componentDidMount() {
		this.setState({countFriends: this.props.usersInfo.length });
	}
	render() {
		return (
			<Panel id={this.props.id}>
				<PanelHeader
					left={<HeaderButton onClick={this.props.go} data-to="home">
						{this.osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
					</HeaderButton>}
				>
					Панель управления.
				</PanelHeader>
				<Group title="">
					<Div style={{display: 'flex', zIndex: 100}}>
						<Button size="l" stretched style={{ marginRight: 8 }}  onClick={this.deleteAllFriends.bind(this)}>Удалить всех друзей</Button>
						<Button size="l" stretched level="secondary" onClick={this.cancelDelFriends.bind(this)}>Отмена удаление</Button>
					</Div>
					{this.state.isDelAllFriends &&
						<Cell description={`удалено ${this.state.progress} / ${this.state.countFriends}`}>
							<Progress value={this.state.progress} />
						</Cell>
					}
				</Group>
				<Group title="Standart size">
					{
						this.state.isDelAllFriends &&
						<Div style={{
								position: 'absolute',
								top: 0,
								bottom: 0,
								left: 0,
								right: 0,
								zIndex: 98,
								background: '#ffffff45'
						}}>
							<Spinner size="large" style={{position: 'fixed', zIndex: 99}} />
						</Div>
					}
					
					{
						this.props.usersInfo.map((friend) => {
							return (
								<Cell
									size="l"
									description={friend.status || 'Статус не установлен'}
									before={<Avatar src={friend.photo_200_orig} />}
									bottomContent={<Button onClick={this.deleteFriend(friend.id)}>Удалить</Button>}
									key={friend.id}
								>
								{`${friend.first_name} ${friend.last_name}`}
								</Cell>
							);
						})
					}
				</Group>
			</Panel>
		);
	}
}

VueFriends.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	access_token: PropTypes.string.isRequired,
	userInfo: PropTypes.array.isRequired
};

export default VueFriends;
